import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {getWardSet,getDepartmentSet,getBedTypeSet,getHospitalSet,getAdmission,getRoomSet,getBedSet, getUserByRole,updateAdmission} from './../Apis.js'

import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';



class UpdateAdmission extends Component {
    constructor(props) {
        super(props);
        this.state = {
           
            visible:false,
            
            showForm:false,
            msgType:'success',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
       
        console.log(values);
        this.updateAdmission(values,setSubmitting,this.props.match.params.id)
       
    }

    updateAdmission=async(values,setSubmitting,id)=>{
          const responseData= await updateAdmission(values,id);
          console.log(responseData,'response')
          if(responseData.data.message)
          {
              this.setState({ visible:true,massege:'Admission Update Update Failed '+responseData.message,msgType:'danger'})
              setSubmitting(false);
          }
          else{
              this.setState({ visible:true,msgType:'success',massege:'Admission Update Update Successfully'})
              setSubmitting(false);
              return;
          }
      }

    
        componentDidMount=async()=>{

            const responseDataAdd= await getAdmission(this.props.match.params.id);
            console.log(responseDataAdd)
            if(responseDataAdd.data)
            {
                this.setState({
                    patient_id: responseDataAdd.data.patient_id,
                    emr_id:responseDataAdd.data.emr_id,
                    hospital_id: responseDataAdd.data.hospital_id,
                    depart_id: responseDataAdd.data.depart_id,
                    ward_id: responseDataAdd.data.ward_id,
                    room_id: responseDataAdd.data.room_id,
                    bed_id: responseDataAdd.data.bed_id,
                    title: responseDataAdd.data.title,
                    description: responseDataAdd.data.description,
                    status: responseDataAdd.data.status,
           
                })
            }
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSet:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 

            const responseDataDepart= await getDepartmentSet();
            console.log(responseDataDepart,'response')
            if(responseDataDepart.status == 200){
                this.setState({departmentSets:responseDataDepart.data.result})
            } else if (responseDataDepart.status == 404) {
                console.warn(responseDataDepart.data.message);
            } 
            
            const responseDataWard= await getWardSet();
            console.log(responseDataWard,'response')
            if(responseDataWard.status == 200){
                this.setState({wardSets:responseDataWard.data.result})
            } else if (responseDataWard.status == 404) {
                console.warn(responseDataWard.data.message);
            } 

            const responseDataBedType= await getBedTypeSet();
            console.log(responseDataBedType,'response')
            if(responseDataBedType.status == 200){
                this.setState({bedtypeSets:responseDataBedType.data.result})
            } else if (responseDataBedType.status == 404) {
                console.warn(responseDataBedType.data.message);
            } 
            const responseDataUserRole= await  getUserByRole();
            console.log(responseDataUserRole,'asdf')
            if(responseDataUserRole.status == 200){
                console.log('asdf',responseDataUserRole)
                this.setState({userRoleSet:responseDataUserRole.data})
            } else if (responseDataUserRole.status == 404) {
                console.warn(responseDataUserRole.data.message);
            } 
           
            const responseDataRoom= await getRoomSet();
            console.log(responseDataRoom,'response')
            if(responseDataRoom.status == 200){
                this.setState({roomSets:responseDataRoom.data.result,showForm:true})

            } else if (responseDataRoom.status == 404) {
                console.warn(responseDataRoom.data.message);
            } 
            const responseDataBed= await getBedSet();
            console.log(responseDataBed,'response')
            if(responseDataBed.status == 200){
                this.setState({bedSets:responseDataBed.data.result})
            } else if (responseDataBed.status == 404) {
                console.warn(responseDataBed.data.message);
            } 
        }
    render() {
        console.log(this.state.hospital_id)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update Admission</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        patient_id: this.state.patient_id,
                                        emr_id:this.state.emr_id,
                                        hospital_id: this.state.hospital_id,
                                        depart_id: this.state.depart_id,
                                        ward_id: this.state.ward_id,
                                        room_id: this.state.room_id,
                                        bed_id: this.state.bed_id,
                                        title: this.state.title,
                                        description: this.state.description,
                                        status: 1,
                                        modified_by:localStorage.getItem('id'),
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            // validationSchema={Yup.object().shape({
                            // email: Yup
                            //     .string()
                            //     .email()
                            //     .required(),
                            // password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // confirm_password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // phone:Yup
                            //     .number()
                            //     .required(),
                            // alternate_phone:Yup
                            //     .number()
                            //     .required(),
                            // })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                            <Form className="form-horizontal">
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Patient</Label>
                                        </Col>
                                        <Col  xs="12" md="9">
                                            <Field 
                                           required={true}
                                            type="text" 
                                            name="patient_id" 
                                            placeholder="Patient Name" 
                                            id="text-input"
                                            className="form-control"
                                            disabled
                                            /> 
                                            <ErrorMessage name="first_name" />
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">EMR Id:</Label>
                                        </Col>
                                        <Col  xs="12" md="9">
                                            <Field 
                                           required={true}
                                            type="text" 
                                            name="emr_id" 
                                            placeholder="EMR Id" 
                                            id="text-input"
                                            className="form-control"
                                            /> 
                                            <ErrorMessage name="first_name" />
                                        </Col> 
                                                                               
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Title:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            type="text" 
                                            name="title" 
                                            placeholder="Title" 
                                            className="form-control"
                                                /> 
                                            <ErrorMessage name="email" />
                                        </Col>                                        
                                    </FormGroup>
                             
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Hospital Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            disabled={true}
                                              component="select" 
                                            name="hospital_id" 
                                            placeholder="Hospital Name " 
                                            className="form-control"
                                            >
                                                 {this.state.hospitalSet&&this.state.hospitalSet.map((hospital, index) =>
                                                 <option key={index} value={hospital.id}>{hospital.name}</option>
                                            )}
                                            </Field> 
                                            {/* <ErrorMessage name="phone" /> */}
                                        </Col>                                      
                                    </FormGroup>
                                     <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Department Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                              component="select" 
                                            name="depart_id" 
                                            placeholder="Department Id" 
                                            className="form-control"
                                            >
                                                 {this.state.departmentSets&&this.state.departmentSets.map((department, index) =>
                                                 <option key={index} value={department.id}>{department.name}</option>
                                            )}
                                            </Field> 
                                             <ErrorMessage name="phone" /> 
                                         </Col>                                         
                                    </FormGroup> 
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Ward Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             component="select" 
                                            name="ward_id" 
                                            placeholder="Ward Id" 
                                            className="form-control"
                                            >
                                                      {this.state.wardSets&&this.state.wardSets.map((ward, index) =>
                                                        <option key={index} value={ward.id}>{ward.name}</option>
                                                    )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                         
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Room No:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             required={true}
                                             component="select" 
                                            name="room_id" 
                                            placeholder="Room NO" 
                                            className="form-control"
                                            >
                                            {this.state.roomSets&&this.state.roomSets.map((room, index) =>
                                              <option key={index} value={room.id}>{room.room_no}</option>
                                          )}
                                            </Field> 
                                            <ErrorMessage name="phone" />
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">                                                           
                                            <Label htmlFor="text-input">Bed No:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             component="select" 
                                            name="ward_id" 
                                            placeholder="Ward Id" 
                                            className="form-control"
                                            >
                                                      {this.state.bedSets&&this.state.bedSets.map((bed, index) =>
                                                        <option key={index} value={bed.id}>{bed.bed_no}</option>
                                                    )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                          
                                    </FormGroup>
                                   
                                    <FormGroup row>
                                        <Col md="3">                                                           
                                            <Label htmlFor="text-input">Status:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            component="select" 
                                            name="status" 
                                            placeholder="Status" 
                                            className="form-control"
                                           >
                                               <option  value={1}>{'Enable'}</option>
                                               <option  value={2}>{'Disable'}</option>
                                        </Field>
                                            <ErrorMessage name="alternate_phone" />
                                        </Col>                                        
                                    </FormGroup>
                                 
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                               Update Admission
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateAdmission;